<template>
  <el-form
    v-loading="formLoading"
    :model="channel"
    :rules="rules"
    ref="dataForm"
    label-width="100px"
    style="margin-top: 15px"
  >
    <el-row>
      <el-col :span="16">
        <el-form-item label="接入点" prop="accessPointId">
          <el-cascader
            :props="typeOptions"
            v-model="channel.accessPointId"
            placeholder="接入点"
            style="width: 100%"
            @change="handleBlur"
          >
          </el-cascader>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8">
        <el-form-item label="通道名称" prop="name">
          <el-input v-model="channel.name" maxlength="20" show-word-limit />
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="单价(元)" prop="price">
          <el-input v-model="channel.price" />
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8">
        <el-form-item label="编码" prop="code">
          <el-input v-model="channel.code" />
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="方法" prop="code">
          <el-input v-model="channel.method" />
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="16">
        <el-form-item label="备注">
          <el-input
            v-model="channel.description"
            :autosize="{ minRows: 2, maxRows: 4 }"
            type="textarea"
            placeholder="请输入"
          />
        </el-form-item>
      </el-col>
    </el-row>
    <el-form-item
      v-for="(param, index) in channel.params"
      :key="param.id"
      :label="'参数' + (index + 1)"
    >
      <el-row>
        <el-col :span="8">
          <el-form-item
            :prop="'params.' + index + '.name'"
            label="参数名"
            :rules="{
              required: true,
              message: '名称不能为空',
              trigger: 'blur'
            }"
          >
            <el-input v-model="param.name" />
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item
            :prop="'params.' + index + '.accessPointName'"
            label="接入名称"
            :rules="{
              required: true,
              message: '名称不能为空',
              trigger: 'blur'
            }"
          >
            <el-input v-model="param.accessPointName" />
          </el-form-item>
        </el-col>
        <el-col :span="2" style="margin-left: 10px">
          <el-button @click.prevent="removeParam(param)">删除</el-button>
        </el-col>
      </el-row>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('dataForm')">保存</el-button>
      <el-button @click="addParam">新增参数</el-button>
      <el-button @click="resetForm()">重置</el-button>
    </el-form-item>
  </el-form>
</template>
<script>
import { list, accessType, get } from "@/api/accessPoint";
import { getChannel, updateChannel, createChannel } from "@/api/supplier";
import { isMoney } from "@/utils/validate";

const defaultForm = {
  id: undefined,
  supplierId: undefined,
  accessPointId: undefined,
  name: "",
  code: "",
  method: "",
  description: "",
  price: 0.0,
  status: 1,
  params: [
    {
      id: "",
      name: "",
      accessPointName: "",
      status: 1
    }
  ]
};

export default {
  props: {
    isEdit: {
      type: Boolean,
      default: false
    }
  },
  created() {
    if (this.isEdit) {
      this.channel.id = this.$route.params && this.$route.params.id;
      this.fetchData(this.channel.id);
    } else {
      this.supplierId = this.$route.params && this.$route.params.supplierId;
      if (!this.supplierId) {
        this.$router.push({ path: "/supplier" });
      }
      this.channel = Object.assign({}, defaultForm);
    }
    accessType({ status: 1 }).then(response => {
      if (response.code === 0) {
        this.codeOptions = response.data.record;
      }
    });
    this.formLoading = false;
  },
  data() {
    const checkMoney = (rule, value, callback) => {
      if (value || value === 0) {
        if (!isMoney(value)) {
          callback(new Error("金额错误"));
        } else {
          callback();
        }
      } else {
        callback(new Error("金额不能为空"));
      }
    };
    return {
      typeOptions: {
        emitPath: false,
        lazy: true,
        lazyLoad(node, resolve) {
          const { level } = node;
          if (level === 0) {
            accessType({ status: 1 }).then(response => {
              if (response.code === 0) {
                this.codeOptions = response.data.record;
                const nodes = this.codeOptions.map(item => ({
                  value: item.id * -1,
                  label: item.name,
                  leaf: false
                }));
                resolve(nodes);
              }
            });
          } else {
            let id = node.data.value * -1;
            list({ status: 1, accessPointTypeId: id }).then(response => {
              if (response.code === 0) {
                this.codeOptions = response.data.record;
                const nodes = this.codeOptions.map(item => ({
                  value: item.id,
                  label: item.name,
                  leaf: true
                }));
                resolve(nodes);
              }
            });
          }
        }
      },
      idx: 1,
      formLoading: true,
      codeOptions: [],
      supplierId: undefined,
      channel: Object.assign({}, defaultForm),
      rules: {
        name: [
          {
            required: true,
            message: "请输入通道名称",
            trigger: "blur",
            max: 20
          }
        ],
        code: [
          {
            required: true,
            message: "请输入Spring编码",
            trigger: "blur",
            max: 20
          }
        ],
        method: [
          { required: true, message: "请输入方法名称", trigger: "blur" }
        ],
        accessPointId: [
          { required: true, message: "请选择接入点", trigger: "blur" }
        ],
        price: [{ required: true, validator: checkMoney, trigger: "blur" }]
      }
    };
  },
  methods: {
    fetchData(id) {
      this.formLoading = true;
      getChannel(id).then(response => {
        if (response.code === 0) {
          let data = response.data;
          this.supplierId = data.supplierId;
          this.channel.supplierId = data.supplierId;
          this.channel.name = data.name;
          this.channel.method = data.method;
          this.channel.code = data.code;
          this.channel.description = data.description;
          this.channel.accessPointId = data.accessPointId;
          this.channel.price = data.price / 100;
          this.channel.params = data.supplierParamList;
          this.idx = data.supplierParamList
            ? data.supplierParamList.length + 1
            : 0;
        }
        this.formLoading = false;
      });
    },
    submitForm() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          let ap = JSON.parse(JSON.stringify(this.channel));
          ap.price = ap.price * 100;
          ap.supplierId = this.supplierId;
          if (this.isEdit) {
            this.update(ap);
          } else {
            this.create(ap);
          }
        }
      });
    },
    create(data) {
      this.formLoading = true;
      createChannel(data).then(response => {
        if (response.code === 0) {
          this.$notify({
            title: "成功",
            message: "保存成功",
            type: "success",
            duration: 2000
          });
          this.$router.push({
            name: "Channel",
            params: { supplierId: this.supplierId }
          });
        }
        this.formLoading = false;
      });
    },
    update(data) {
      this.formLoading = true;
      updateChannel(data).then(response => {
        if (response.code === 0) {
          this.$notify({
            title: "成功",
            message: "保存成功",
            type: "success",
            duration: 2000
          });
          this.$router.push({
            name: "Channel",
            params: { supplierId: this.supplierId }
          });
        }
        this.formLoading = false;
      });
    },
    resetForm() {
      this.channel = Object.assign({}, defaultForm)
      this.$refs['dataForm'].resetFields();
    },
    removeParam(item) {
      var index = this.channel.params.indexOf(item);
      if (index !== -1) {
        this.channel.params.splice(index, 1);
      }
    },
    addParam() {
      this.channel.params.push({
        id: this.idx++,
        name: "",
        accessPointName: "",
        status: 1
      });
    },
    handleBlur(value) {
      this.formLoading = true;
      if (value) {
        get(value).then(response => {
          if (response.code === 0) {
            const data = response.data;
            this.channel.name = data.name;
            if (data.accessPointParamList) {
              for (const p of data.accessPointParamList) {
                this.channel.params.push({
                  id: this.idx++,
                  name: p.name,
                  accessPointName: p.name,
                  status: p.status
                });
              }
            }
          }
        });
      }
      this.formLoading = false;
    }
  }
};
</script>
